import React from 'react';
import { Box, Image, Text, Badge, Flex, Button, Avatar, AvatarBadge, HStack } from '@chakra-ui/react';
import { motion } from 'framer-motion';
import './UserCard.css';

const MotionBox = motion(Box);

const UserCard = ({ username, description, tags, imageUrl, isConnected, onCancel, onLike, animation }) => {
  return (
    <MotionBox
      className="card-container"
      initial={{ opacity: 0, scale: 0.8 }}
      animate={animation || { opacity: 1, scale: 1 }}
      exit={{ opacity: 0, scale: 0.5 }}
    >
      {/* User Image with Connection Status */}
      <Flex justify="flex-end" position="relative">
        <Avatar size="lg" src={imageUrl} name={username}>
          <AvatarBadge
            boxSize="1.25em"
            bg={isConnected ? 'green.500' : 'red.500'}
          />
        </Avatar>
      </Flex>

      {/* User Tags */}
      <HStack spacing={2} mt={4} flexWrap="wrap" justify="center">
        {tags.map((tag, index) => (
          <Badge
            key={index}
            colorScheme="blue"
            variant="solid"
            borderRadius="full"
            px={2}
            py={1}
          >
            {tag}
          </Badge>
        ))}
      </HStack>

      {/* User Description */}
      <Text mt={4} fontSize="sm" textAlign="center">
        {description}
      </Text>

      {/* Action Buttons */}
      <Flex mt={6} justify="space-around">
        <Button colorScheme="red" variant="outline" onClick={onCancel} w="45%">
          Cancel
        </Button>
        <Button colorScheme="green" onClick={onLike} w="45%">
          Like
        </Button>
      </Flex>
    </MotionBox>
  );
};

export default UserCard;
